import pawHideIcon from "@/assets/pawHide.png";
import pawShowIcon from "@/assets/pawShow.png";
export default {
  data() {
    return {
      showPositionValue: false, //提示信息
      toastContent: "",
      topTitle: "修改登陆密码",
      pawIcon1: pawHideIcon,
      pawIcon2: pawHideIcon,
      pawIcon3: pawHideIcon,
      oldPwdType: "password",
      newPwdType: "password",
      againNewPwdType: "password",
      oldPwd: "",
      newPwd: "",
      againNewPwd: ""
    };
  },
  methods: {
    getPwd() {
      var that = this,
        formData,
        reg = /^(?![\d]+$)(?![a-zA-Z]+$)(?![^\da-zA-Z]+$).{8,20}$/;
      if (that.oldPwd == "" || that.newPwd == "") {
        that.showPositionValue = true;
        that.toastContent = "密码不能为空";
      } else if (that.newPwd.length < 8 || that.newPwd.length >= 20) {
        that.showPositionValue = true;
        that.toastContent = "密码长度8-20个字符";
      }else if (that.oldPwd ==that.newPwd) {
        that.showPositionValue = true;
        that.toastContent = "新密码与原密码不能一致";
      } else if (that.newPwd != that.againNewPwd) {
        that.showPositionValue = true;
        that.toastContent = "新密码和再次输入密码不一致";
      } else if (!reg.test(that.newPwd)) {
        that.showPositionValue = true;
        that.toastContent = "密码不能为纯数字或纯字母";
      } else {
        formData = {
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token,
          oldpassword: that.oldPwd,
          password: that.newPwd,
          second_password: that.againNewPwd
        };
        that
          .$request({
            method: "POST",
            url: that.$baseURL + "/personal/set-password",
            data: that.$qs.stringify(formData)
          })
          .then(function(response) {
            if (response.code == 0) {
              // that.oldPwd=""; that.newPwd=""; that.againNewPwd="";
              that.showPositionValue = true;
              that.toastContent = response.errmsg;
              that.$router.go(-1);
            } else {
              that.showPositionValue = true;
              that.toastContent = response.errmsg;
            }
          })
          .catch(function(error) {
            console.log(error);
          });
      }
    },
    getOldPWD() {
      var that = this,
        formData = {
          access_token: that.$store.state.user.token,
          password: that.oldPwd
        };
      that
        .$request({
          method: "POST",
          url: that.$baseURL + "/personal/check-password",
          data: that.$qs.stringify(formData)
        })
        .then(function(response) {
          if (response.code == 0) {
            that.showPositionValue = true;
            that.toastContent = "原密码" + response.errmsg;
          } else {
            that.showPositionValue = true;
            that.toastContent = "原密码" + response.errmsg;
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    //密码显示or加密
    changeType1() {
      var that = this;
      if (that.oldPwdType == "password") {
        that.oldPwdType = "text";
        that.pawIcon1 = pawShowIcon;
      } else {
        that.oldPwdType = "password";
        that.pawIcon1 = pawHideIcon;
      }
    },
    changeType2() {
      var that = this;
      if (that.newPwdType == "password") {
        that.newPwdType = "text";
        that.pawIcon2 = pawShowIcon;
      } else {
        that.newPwdType = "password";
        that.pawIcon2 = pawHideIcon;
      }
    },
    changeType3() {
      var that = this;
      if (that.againNewPwdType == "password") {
        that.againNewPwdType = "text";
        that.pawIcon3 = pawShowIcon;
      } else {
        that.againNewPwdType = "password";
        that.pawIcon3 = pawHideIcon;
      }
    }
  }
};



