import Icon from "@/assets/icon.png";
import systemIcon from "@/assets/systemIcon.png";
import srcIcon from "@/assets/gantanghao.png";
import Clipboard from "clipboard";
import IDCard from "@/assets/IDCard.png";
import Copy from "@/assets/fuzhi.png";
import orderIcon from "@/assets/orderIcon.png";
import myWallet from "@/assets/myWallet.png";
import shouYi from "@/assets/shouYi.png";
import friends from "@/assets/friends.png";
import phb from "@/assets/phb.png";
import kf from "@/assets/kf.png";
import pawHideIcon from "@/assets/noEyes.png";
import pawShowIcon from "@/assets/eyes.png";

export default {
  data() {
    return {
      // isShowIdCard: true,
      dataVal: {
        email: "",
        id_card: "",
        phone: "",
        uid: 0,
        user_name: ""
      },
      btc_total: "",
      btc_total_rmb: "",
      btc_yesterday_income_rem: "",
      btc_total_income_rem: "",
      userName: "",
      orderIcon: orderIcon,
      myWallet: myWallet,
      shouYi: shouYi,
      kf: kf,
      IDCard: IDCard,
      friends:friends,
      phb: phb,
      Icon: Icon,
      Copy: Copy,
      eyesIcon: pawHideIcon,
      numVal: false,
      toastVAl: false,
      toastText: "",
      systemIcon: systemIcon,
      srcIcon: srcIcon,
      list: {},
      ranking: 0,
      activity_id:""
    };
  },
  filters: {
    uppercase: function(value) {
      if (!value) {
        return "";
      }
      return value.toUpperCase();
    }
  },
  mounted() {
    this.getSelf();
    this.getCoin();
    this.getCoinTotal();
    this.getAbacus();
    this.getActivityID();
  },
  methods: {
    getCoinTotal() {
      var that = this,
        formData = {
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token
        };
      that
        .$request({
          method: "POST",
          url: that.$baseURL + "/wallet/wallet-balance",
          data: that.$qs.stringify(formData)
        })
        .then(function(response) {
          that.btc_total = response.data.btc_total;
          that.btc_total_rmb = response.data.btc_total_rmb;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    getCoin() {
      var that = this,
        formData = {
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token
        };
      that
        .$request({
          method: "POST",
          url: that.$baseURL + "/income/order-income-total",
          data: that.$qs.stringify(formData)
        })
        .then(function(response) {
          that.list = response.data;
          that.btc_yesterday_income_rem = parseFloat(
            (
              Number(response.data.btc_yesterday_income) *
              Number(that.$store.state.user.bct_cnc)
            ).toFixed(8)
          );
          that.btc_total_income_rem = parseFloat(
            (
              Number(response.data.btc_total_income) *
              Number(that.$store.state.user.bct_cnc)
            ).toFixed(8)
          );
          // that.$store.state.user.bct_cnc
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    getSelf() {
      var that = this,
        formData = {
          //  sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token
        };
      that
        .$request({
          method: "POST",
          url: that.$baseURL + "/personal/user-detail",
          data: that.$qs.stringify(formData)
        })
        .then(function(response) {
          that.$store.dispatch("setUID", response.data.uid);
          that.dataVal = response.data;
          if (response.data.user_name != "") {
            that.userName = response.data.user_name.substring(0, 1) + "**";
          } else if (response.data.phone != "") {
            that.userName = response.data.phone.replace(
              /(\d{3})(\d{4})(\d{4})/,
              "$1****$3"
            );
          } else if (response.data.email != "") {
            if (String(response.data.email).indexOf("@") > 0) {
              let str = response.data.email.split("@"),
                _s = "";

              if (str[0].length > 4) {
                _s = str[0].substr(0, 3);
                for (let i = 0; i < str[0].length - 4; i++) {
                  _s += "*";
                }
              } else {
                _s = str[0].substr(0, 1);
                for (let i = 0; i < str[0].length - 1; i++) {
                  _s += "*";
                }
              }
              that.userName = _s + "@" + str[1];
              return that.userName;
            }
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    // 是否加密
    getEyesData() {
      if (this.numVal == false) {
        this.eyesIcon = pawShowIcon;
        this.numVal = true;
      } else {
        this.eyesIcon = pawHideIcon;
        this.numVal = false;
      }
    },
    setUpfun() {
      this.$router.push("/setUp");
    },
    copyWord() {
      var that = this;
      var clipboard = new Clipboard(".NameTxt");

      clipboard.on("success", function(e) {
        that.toastVAl = true;
        that.toastText = "复制成功";
        e.clearSelection();
      });

      clipboard.on("error", function(e) {
        console.error("Action:", e.action);
        console.error("Trigger:", e.trigger);
      });
    },
    //当前用户排行详情
    getAbacus() {
      var that = this,
        formData = {
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token
        };
      that
        .$request({
          method: "POST",
          url: that.$baseURL + "/abacus/abacus-detail",
          data: that.$qs.stringify(formData)
        })
        .then(function(response) {
          if (response.code == 0) {
            if (response.data.ranking) {
              that.ranking = response.data.ranking;
            } else {
              that.ranking = 0;
            }
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    // 活动ID
    getActivityID() {
      var that = this,
        formData = {
          link: "邀请好友"
        };
      that
        .$request({
          method: "POST",
          url: that.$baseURL + "/invite/get-activity",
          data: that.$qs.stringify(formData)
        })
        .then(function(response) {
          that.activity_id = response.data.activity_id;
        })
        .catch(function(error) {
          console.log(error);
        });
    }
    // 身份认证
    // getRZIdcard() {
    //   this.$router.push("/setIDCard");
    // },
    // setCloseIdCard(){
    //   this.isShowIdCard=false;
    // }
  }
};



