
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

import xiaolaba from "@/assets/xiaolaba.png";
import gengduo2 from "@/assets/gengduo2.png";
import xszn from "@/assets/xszn.png";
import newPeople from "@/assets/newPeople.png";
import btc from "@/assets/btc.png";
import vSwiper from "./Swiper";
export default {
data() {
return {
btc: btc,
xiaolabaIcon: xiaolaba,
gengduo2: gengduo2,
newXszn: xszn,
newPeople: newPeople,
newUser: 1,
list: [],
noticeList: [],
coinAPI: { bct_cnc: "0", bct_usdt: "0", difficulty: "0" },
bannerList: [
],
swiperOption: {
showDots: true, // 是否显示分页器
interval: 3000, // 轮播间隔时间，默认3s
autoplay: true, // 是否自动播放
loop: true, // 是否循环轮播
},
activity_id: "",
};
},
created() {
this.getSwiper();
this.getNotice();
this.getGoodsList();
this.getCoin();
},
mounted() {
this.getNewUser();
this.getActivityID();
},
components: { Swiper, SwiperItem, vSwiper },
methods: {

getSwiper() {
var that = this;












var arr=[{img:"https://m.qpic.cn/psc?/V54YgPbm2FOgzv0F7kJN05G20N08nvwB/bqQfVz5yrrGYSXMvKr.cqUlirBWnOZn62Mo0ccTj8w*hCWlyzuj7HjlPIhjAFkwHHw095L7*16emyVvEHWxVl4tGwbYuy.X9DfpjLmfj0Y0!/b&bo=vgIYAQAAAAADB4c!&rf=viewer_4"},
{img:"http://m.qpic.cn/psc?/V54YgPbm2FOgzv0F7kJN05G20N08nvwB/TmEUgtj9EK6.7V8ajmQrEEjNvBUrJvjeab3vsy*O61U7urBEV2F*AftBRXa9tfweGk5ZRxl*PeVLuCKLmZpqkJTQGx1X03eH01ObpCK3TRw!/b&bo=vgIYAQAAAAADJ6c!&rf=viewer_4"},
{img:"http://m.qpic.cn/psc?/V54YgPbm2FOgzv0F7kJN05G20N08nvwB/TmEUgtj9EK6.7V8ajmQrEI1ryYD0v6n7yRdIV0LH8*KhgCO1XPzvpezyA367fMBCR0XI.azwkJodGx9b.txpdLqDQsJczgqVCCz.Pg*2aSc!/b&bo=vgIYAQAAAAADJ6c!&rf=viewer_4"}]



this.bannerList = arr;




},
handleSwiperClick() {},

noticeListFun() {
this.$router.push("/noticeList");
},

getNotice() {
var dataVal = {
num: 6,
},
that = this;
that
.$request({
method: "POST",
url: "/index/notice",
data: that.$qs.stringify(dataVal),
})
.then(function (response) {
that.noticeList =response.list;
})
.catch(function (error) {});
},

getGoodsList() {
var that = this;
that
.$request({
method: "POST",
url:"/index/goods-list",
})
.then(function (response) {
that.list = response.data;
response.data.forEach((val) => {
val.tag = val.tag.split(",");
});
})
.catch(function (error) {});
},

getNewUser() {
var that = this,
formData = {
sign: that.$store.state.user.sign,
access_token: that.$store.state.user.token,
};
that
.$request({
method: "POST",
url: that.$baseURL + "/personal/new-user",
data: that.$qs.stringify(formData),
})
.then(function (response) {
that.newUser = response.data;
})
.catch(function (error) {});
},
setRegister() {
this.$router.push({
name: "NoviceActivityRules",
params: {
activity_id: this.activity_id,
},
});
},

getCoin() {
var that = this;
that
.$request({
method: "POST",
url: that.$baseURL + "/other/coin-api",
})
.then(function (response) {
that.coinAPI = response.data;
that.$store.dispatch("setBctCnc", response.data.bct_cnc);
})
.catch(function (error) {});
},

getActivityID() {
var that = this,
formData = {
link: "邀请好友",
};
that
.$request({
method: "POST",
url: that.$baseURL + "/invite/get-activity",
data: that.$qs.stringify(formData),
})
.then(function (response) {
that.activity_id = response.data.activity_id;
})
.catch(function (error) {
console.log(error);
});
},
},
};



