import pawHideIcon from "../../assets/pawHide.png";
import pawShowIcon from "../../assets/pawShow.png";
// import weixuanzhong from "../../assets/weixuanzhong.png";
// import yixuanzhong from "../../assets/yixuanzhong.png";
import wenhao from "../../assets/wenhao.png";
export default {
  data() {
    return {
      PassWord: "",
      PassWordAgain: "",
      pawIcon: pawHideIcon,
      pawIcon1: pawHideIcon,
      // weixuanzhong:weixuanzhong,
      // yixuanzhong:yixuanzhong,
      wenhao: wenhao,
      pwdType1: "password",
      pwdType: "password",
      checkVal: false,
      showPositionValue: false, //提示信息
      toastContent: "",
      disabled: true
    };
  },
  methods: {
    //我同意
    getCheckIcon() {
      // if (this.checkVal == false) {
      //   this.disabled = false;
      //   this.checkVal = true;
      // } else {
      //   this.disabled = true;
      //   this.checkVal = false;
      // }
      if (this.checkVal == false) {
        this.disabled = true;
        this.checkVal == false;
      } else {
        this.disabled = false;
        this.checkVal == true;
      }
    },
    //密码明文or加密
    changeType1() {
      if (this.pwdType1 === "password") {
        this.pwdType1 = "text";
        this.pawIcon1 = pawShowIcon;
      } else {
        this.pwdType1 = "password";
        this.pawIcon1 = pawHideIcon;
      }
    },
    changeType() {
      if (this.pwdType === "password") {
        this.pwdType = "text";
        this.pawIcon = pawShowIcon;
      } else {
        this.pwdType = "password";
        this.pawIcon = pawHideIcon;
      }
    },
    //立即登录
    loginFun() {
      this.$router.push("/login");
    },
    //注册
    getRegister() {
      var that = this,
        nameVal,
        urlData,
        formData,
        reg = /^(?![\d]+$)(?![a-zA-Z]+$)(?![^\da-zA-Z]+$).{8,20}$/;
      if (that.PassWord != that.PassWordAgain) {
        that.showPositionValue = true;
        that.toastContent = "两次密码输入不一致";
      } else if (that.PassWord.length < 8 || that.PassWord.length >= 20) {
        that.showPositionValue = true;
        that.toastContent = "密码长度8-20个字符";
      } else if (that.PassWord.length == 0) {
        that.showPositionValue = true;
        that.toastContent = "密码不能为空";
      } else if (!reg.test(that.PassWord)) {
        that.showPositionValue = true;
        that.toastContent = "密码不能为纯数字或纯字母";
      } else {
        formData = {
          phone: that.$route.params.phone,
          email: that.$route.params.email,
          type: that.$route.params.type,
          password: that.PassWord,
          second_password: that.PassWordAgain,
          code: that.$route.params.code,
          sign: that.$store.state.user.sign,
          timestamp: that.$store.state.user.timestamp,
          nonce: that.$store.state.user.nonce
        };
        if (that.$route.params.getForget) {
          urlData = "/user/forget-password";
          nameVal = "找回密码";
        } else {
          urlData = "/user/set-password";
          nameVal = "注册";
        }
        that
          .$request({
            method: "POST",
            url: that.$baseURL + urlData,
            data: that.$qs.stringify(formData)
          })
          .then(function(response) {
            if (response.code == 0) {
              that.$router.push("/login");
            } else {
              that.showPositionValue = true;
              that.toastContent = response.errmsg;
            }
          })
          .catch(function(error) {});
      }
    },
    // 协议
    getXieYi() {
      this.$router.push("/PawXieyi");
    }
  }
};



