import icon from "../../assets/gangtanhao.png";
export default {
  data() {
    return {
      icon: icon,
      userName: "",
      disabled: true,
      showPositionValue: false, //提示信息
      toastContent: ""
    };
  },
  methods: {
   
    //按钮是否禁用
    keyDown() {
      if (this.phoneNem != "") {
        this.disabled = false;
      } else {
        this.disabled = true;
      }
    },
    //注册邮箱
    EmailFun() {
      this.$router.push("/eamilRegister");
    },
    //立即登录
    loginFun() {
      this.$router.push("/login");
    },
    //提示框 下一步
    submitFun() {
      var that = this,
        paramsVal,
        formData,
        formData1;
      if (that.userName == "") {
        that.showPositionValue = true;
        that.toastContent = "请您输入邮箱或手机号码";
      } else {
        if (that.userName.search("@") != -1) {
          formData1 = {
            phone: "",
            email: that.userName,
            type: "2"
          };
        } else {
          formData1 = {
            phone: that.userName,
            email: "",
            type: "1"
          };
        }
        that
          .$request({
            method: "POST",
            url: that.$baseURL + "/user/check-user",
            data: that.$qs.stringify(formData1)
          })
          .then(function(response) {
            if (response.code == 0) {
              that.showPositionValue = true;
              that.toastContent = "您输入邮箱或手机号码未注册";
            } else {
              if (that.userName.search("@") != -1) {
                formData = {
                  phone: "",
                  email: that.userName,
                  type: "2",
                  sign: that.$store.state.user.sign,
                  timestamp: that.$store.state.user.timestamp,
                  nonce: that.$store.state.user.nonce
                };
                paramsVal = {
                  phone: "",
                  email: that.userName,
                  type: "2",
                  getForget: 1
                };
              } else {
                formData = {
                  phone: that.userName,
                  email: "",
                  type: "1",
                  sign: that.$store.state.user.sign,
                  timestamp: that.$store.state.user.timestamp,
                  nonce: that.$store.state.user.nonce
                };
                paramsVal = {
                  phone: that.userName,
                  email: "",
                  type: "1",
                  getForget: 1
                };
              }
              that
                .$request({
                  method: "POST",
                  url: that.$baseURL + "/user/get-code",
                  data: that.$qs.stringify(formData)
                })
                .then(function(response) {
                  if (response.code == 0) {
                    that.$router.push({
                      name: "validCode",
                      params: paramsVal
                    });
                  } else {
                    that.showPositionValue = true;
                    that.toastContent = "验证码" + response.errmsg;
                  }
                })
                .catch(function(error) {});
            }
          })
          .catch(function(error) {});
      }
    }
  }
};



