import btc from "@/assets/btc.png";
import usdt from "@/assets/usdt.png";
import fil from "@/assets/fil.png";
import expiredTab from "./component/expiredTab";
import earningsTab from "./component/earningsTab";
export default {
  data() {
    return {
      topTitle: "",
      selected: 0,
      componentView: "earningsTab",
      active: 0,
      src: btc,
      list: {
        btc_coin_type: "",
        btc_total_income: "",
        btc_yesterday_income: ""
      },
      tabs: [
        {
          type: "收益中",
          view: "earningsTab"
        },
        {
          type: "已到期",
          view: "expiredTab"
        }
      ]
    };
  },
  components: { expiredTab, earningsTab },
  filters: {
    uppercase: function(value) {
      if (!value) {
        return "";
      }
      return value.toUpperCase();
    }
  },
  mounted() {
    this.getCoin();
  },
  methods: {
    getCoin() {
      var that = this,
        formData = {
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token
        };
      that
        .$request({
          method: "POST",
          url: that.$baseURL + "/income/order-income-total",
          data: that.$qs.stringify(formData)
        })
        .then(function(response) {
          
          if(response.data.btc_yesterday_income==null){
            response.data.btc_yesterday_income="0.00000000";
          }
          if(response.data.btc_total_income==null){
            response.data.btc_total_income="0.00000000";
          }
          if(response.data.btc_coin_type==null){
            response.data.btc_coin_type="0.00000000";
          }
          that.list = response.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    toggle(i, v) {
      this.active = i;
      this.componentView = v;
    }
  }
};



