import logo from "@/assets/logo.png";
import pawHideIcon from "@/assets/pawHide.png";
import pawShowIcon from "@/assets/pawShow.png";
// import { mapGetters } from "vuex";
export default {
  data() {
    return {
      logo: logo,
      pawIcon: pawHideIcon,
      inshow: false,
      form: {
        userName: "15013698713",
        ueserPaw: "btys2018"
      },
      pwdType: "password",
      showPositionValue: false, //提示信息
      toastContent: ""
    };
  },
  computed: {
    // ...mapGetters(["getTimestamp", "getNonce", "getSign" ])
  },
  mounted() {
    this.getSign();
  },
  methods: {
    //获取签名TODO
    getSign() {
      var that = this;
      that
        .$request({
          method: "POST",
          // url: that.$baseURL + "/user/sign"
        })
        .then(function(response) {
          that.$store.dispatch("setSign", "response.data.sign");
          that.$store.dispatch("setTimestamp", "response.data.timestamp");
          that.$store.dispatch("setNonce", "response.data.nonce");
        })
        .catch(function(error) {});
    },
    //
    //密码显示or加密
    changeType() {
      if (this.pwdType === "password") {
        this.pwdType = "text";
        this.pawIcon = pawShowIcon;
      } else {
        this.pwdType = "password";
        this.pawIcon = pawHideIcon;
      }
    },
    //注册
    registerFun() {
      this.$router.push("/phoneRegister");
    },
    //找回密码
    retrieveFun() {
      this.$router.push("/retrievePaw");
    },
    //提示框
    submitFun() {
      var that = this;
      if (that.form.userName == "") {
        that.showPositionValue = true;
        that.toastContent = "请您输入邮箱或手机号码";
      } else if (that.form.ueserPaw == "" || that.form.ueserPaw.length < 8) {
        that.showPositionValue = true;
        that.toastContent = "请您输入8-20位密码";
      } else {
        that. getCheckUser(that.form.userName);
        // that. getRequest();
      }
    },
    getRequest(){
       var that = this,
        formData;
       if (that.form.userName.search("@") != -1) {
          formData = {
            phone: "",
            email: that.form.userName,
            type: "2",
            password: that.form.ueserPaw,
            sign: that.$store.state.user.sign,
            timestamp: that.$store.state.user.timestamp,
            nonce: that.$store.state.user.nonce
          };
        } else {
          formData = {
            phone: that.form.userName,
            email: "",
            type: "1",
            password: that.form.ueserPaw,
            sign: that.$store.state.user.sign,
            timestamp: that.$store.state.user.timestamp,
            nonce: that.$store.state.user.nonce
          };
        }
        // that
        //   .$request({
        //     method: "POST",
        //     url: "/user/login",
        //     data: that.$qs.stringify(formData)
        //   })
        //   .then(function(response) {
        //     if (response.code == 0) {
              that.$store.dispatch("setToken"," response.data");
              // that.$store.dispatch("UserName", that.form.userName);
              that.showPositionValue = true;
              that.toastContent = "response.errmsg";
              that.$router.push({ path: "/index/index" });
          //   } else {
          //     that.showPositionValue = true;
          //     that.toastContent = response.errmsg;
          //   }
          // })
          // .catch(function(error) {});
    },
    //校验邮箱和手机
    getCheckUser(val) {
      var that = this,
        formData;
      if (val.search("@") != -1) {
        formData = {
          phone: "",
          email: val,
          type: "2"
        };
      } else {
        formData = {
          phone: val,
          email: "",
          type: "1"
        };
      }
      // that
      //   .$request({
      //     method: "POST",
      //     url: that.$baseURL + "/user/check-user",
      //     data: that.$qs.stringify(formData)
      //   })
      //   .then(function(response) {
      //     if (response.code == 0) {
      //       that.showPositionValue = true;
      //       that.toastContent = "您输入邮箱或手机号码未注册";
      //     }else{
            that. getRequest();
      //     }
      //   })
      //   .catch(function(error) {});
    },
  }
};



