
const user = {
    // namespaced: true,
    state: {
        token: "",
        timestamp:"",
        nonce:"",
        sign:"",
        bct_cnc:"",// 第三方币价接口
        uid:""
    },
    getters: {
        getToken: state => { return state.token},
        getSign: state => { return state.sign},
        getTimestamp:state => { return state.timestamp},
        getNonce:state => { return state.nonce},
        getBctCnc:state => { return state.bct_cnc},
        getUID:state => { return state.uid}
    },
    mutations: {//同步操作
        setBctCnc: (state, t) => {
            // 第三方币价接口
            state.bct_cnc = t;
        },
        setToken: (state, t) => {
            //登录
            state.token = t;
        },
        //签名 --start 
        setSign:(state,t)=>{
            state.sign = t;
        },
        setTimestamp:(state,t)=>{
            state.timestamp = t;
        },
        setNonce:(state,t)=>{
            state.nonce = t;
        },
        setUID:(state,t)=>{
            state.uid = t;
        },
        ///--end
        UserLogout: (state) => {
            //登出的时候要清除token
            state.token = null;
            state.timestamp = null;
            state.nonce = null;
            state.sign = null;
        },
    },
    actions: {//异步操作
        setBctCnc({ commit }, state ) {
            commit('setBctCnc', state);
        },
        //登录
        setToken({ commit }, state ) {
            commit('setToken', state);
            // setToken("Token",state);
        },
        //获取签名
        setSign({ commit }, state ) {
            commit('setSign', state);
        },
        setTimestamp({ commit }, state ) {
            commit('setTimestamp', state);
        },
        setNonce({ commit }, state ) {
            commit('setNonce', state);
        },
        setUID({ commit }, state ) {
            commit('setUID', state);
        },
        //退出登录
        UserLogout({ commit }) {
            commit('UserLogout');
            // removeToken('Token');
            // removeToken('sign');
        }
    }
}

export default user



