export default {
  data() {
    return {
      disableInp: false,
      inputList: [
        { val: "" },
        { val: "" },
        { val: "" },
        { val: "" },
        { val: "" },
        { val: "" }
      ],
      CodeAll: "",
      validCode: "重新发送",
      disabled: true,
      isResend: false,
      disableVal: false,
      showPositionValue: false, //提示信息
      toastContent: ""
    };
  },
  mounted() {
    this.validateBtn();
    // if (this.$route.params.userName != undefined) {

    // }
  },
  methods: {
    //按钮是否禁用
    keyDown() {
      if (this.inputList[4].val != "") {
        this.disabled = false;
      } else {
        this.disabled = true;
      }
    },
    //获取焦点
    nextFocus(el, index) {
      var dom = document.getElementsByClassName("inputStyle"),
        currInput = dom[index],
        nextInput = dom[index + 1],
        lastInput = dom[index - 1];
      /*这里的keyCode 根据不同的平台或许不同,安卓就是不是8*/
      if (el.keyCode != 8) {
        if (index < this.inputList.length - 1) {
          if (this.inputList[index].val.length >= 2) {
            this.inputList[index].val = this.inputList[index].val.slice(0, 1);
          }
          nextInput.focus();
          this.keyDown();
        } else {
          currInput.blur();
          // this.$refs.inputStyle[index].disabled=true;
        }
      } else {
        if (index != 0) {
          lastInput.focus();
        }
      }
    },
    /*当键盘按下的时候清空原有的数*/
    changeValue(index) {
      this.inputList[index].val = "";
    },
    //下一步
    getVal() {
      if (this.CodeAll != "") {
        this.CodeAll = "";
      } else {
        for (let i = 0; i < this.inputList.length; i++) {
          let res = this.inputList[i].val;
          this.CodeAll += res;
        }
      }
      var that = this,
        urlData,
        formVal = {
          phone: that.$route.params.phone,
          email: that.$route.params.email,
          type: that.$route.params.type,
          code: that.CodeAll,
          sign: that.$store.state.user.sign,
          timestamp: that.$store.state.user.timestamp,
          nonce: that.$store.state.user.nonce
        };
      if (that.$route.params.getForget) {
        urlData = "/user/check-forget-code";
      } else {
        urlData = "/user/check-code";
      }
      that
        .$request({
          method: "POST",
          url: that.$baseURL + urlData,
          data: that.$qs.stringify(formVal)
        })
        .then(function(response) {
          if (response.code == 0) {
            that.$router.push({
              name: "SetPassword",
              params: {
                phone: that.$route.params.phone,
                email: that.$route.params.email,
                type: that.$route.params.type,
                code: that.CodeAll,
                getForget: that.$route.params.getForget
              }
            });
          } else {
            that.showPositionValue = true;
            that.toastContent = response.errmsg;
            that.CodeAll = "";
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    //立即登录
    loginFun() {
      this.$router.push("/login");
    },
    //重新发送验证码
    resend() {
      var that = this;
      var formVal = {
        phone: that.$route.params.phone,
        email: that.$route.params.email,
        type: that.$route.params.type,
        sign: that.$store.state.user.sign,
        timestamp: that.$store.state.user.timestamp,
        nonce: that.$store.state.user.nonce
      };
      that
        .$request({
          method: "POST",
          url: that.$baseURL + "/user/get-code",
          data: that.$qs.stringify(formVal)
        })
        .then(function(response) {
          if (response.code == 0) {
            that.validateBtn();
            that.showPositionValue = true;
            that.toastContent = response.errmsg;
          } else {
            that.showPositionValue = true;
            that.toastContent = response.errmsg;
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    //倒计时
    validateBtn() {
      // this.showPositionValue = true;
      // this.toastContent =
      //   "已发送验证码到" + this.$route.params.userName;
      let time = 60;
      let timer = setInterval(() => {
        if (time <= 0) {
          clearInterval(timer);
          this.disableVal = false;
          this.isResend = false;
          this.validCode = "获取验证码";
        } else {
          this.validCode = time + "秒后重试";
          this.disableVal = true;
          this.isResend = true;
          time--;
        }
      }, 1000);
    }
  }
};



