
const common = {
    // namespaced: true,
    state: {
        id: "",
        item:"",
        chain_coin_type:"",
        isConfirmPayment:"",
        order_id:""
        // orderList:""
    },
    getters: {
        getID: state => { return state.id},
        getchain_coin_type:state => { return state.chain_coin_type},
        getisConfirmPayment:state => { return state.isConfirmPayment},
        getorder_id:state => { return state.order_id},
        // getorderList:state => { return state.orderList}
    },
    mutations: {//同步操作
        setID: (state, t) => {
            state.id = t;
        },
        setchain_coin_type: (state, t) => {
            state.chain_coin_type = t;
        },
        setisConfirmPayment: (state, t) => {
            state.isConfirmPayment = t;
        },
        setorder_id: (state, t) => {
            state.order_id = t;
        },
        // setorderList: (state, t) => {
        //     state.orderList = t;
        // }
       
    },
    actions: {//异步操作
        setID({ commit }, state ) {
            commit('setID', state);
        },
        setchain_coin_type({ commit }, state ) {
            commit('setchain_coin_type', state);
        },
        setisConfirmPayment({ commit }, state ) {
            commit('setisConfirmPayment', state);
        },
        setorder_id({ commit }, state ) {
            commit('setorder_id', state);
        // },
        // setorderList({ commit }, state ) {
        //     commit('setorderList', state);
        }
    }
}

export default common



