import noPay from "./component/noPay";
import toPay from "./component/toPay";
import earnings from "./component/earnings";
import expired from "./component/expired";
export default {
  data() {
    return {
      tabIndex: 0,
      topTitle: "我的订单",
      active: Number,
      currentView: String,
      tabs: [
        {
          type: "待支付",
          view: "noPay"
        },
        {
          type: "已支付",
          view: "toPay"
        },
        {
          type: "收益中",
          view: "earnings"
        },
        {
          type: "已到期",
          view: "expired"
        }
      ]
    };
  },
  components: {
    toPay,
    noPay,
    expired,
    earnings
  },
  mounted() {
    if (this.$route.params.index == "one") {
      this.active = 1;
      this.currentView = "toPay";
    } else {
      this.active = 0;
      this.currentView = "noPay";
    }
  },
  methods: {
    toggle(i, v) {
      this.active = i;
      this.currentView = v;
    }
  }
};



