import Vue from 'vue'
// import "babel-polyfill"
import Vuex from 'vuex'

Vue.use(Vuex)


import user from "./modules/user"
import common from "./modules/common"

const store = new Vuex.Store({
    modules: {
        user,
        common
    },

})

export default store;



